import React, { useEffect, useState, useCallback, useMemo } from 'react'
import { shallowEqual, useDispatch, useSelector } from 'react-redux'
import { SET_AUTOCOMPLETE_HANDLE, SET_FEED_OBJECT, SET_SELECTED_LIST, TIKTOK_HASHTAG_ID_URL } from '../../../../../../../actions/addFeedActions'
import { Form, InputGroup, Col, Row, Dropdown } from 'react-bootstrap'
import { useParams } from 'react-router-dom'
import AutoCompleteBlock from '../../../AddFeed/component/AutoCompleteBlock'
import SearchPlace from '../../../AddFeed/component/SearchPlace'
import Select from 'react-select'
import { FontAwesomeIcon } from '../../../../../../../Assets/AssetsHelper'
import { debounce } from '../../../../../../../utils'
import { AUTOCOMPLETE_INPUT, getInputIcon, getInputPlaceholder } from '../../utils';


const TabInputTab = ({ filterData, activeTab, countries, category }) => {
    let { networkId } = useParams()
    const dispatch = useDispatch()
    const [showList, setShowList] = useState(false)
    const [networkText, setNetworkText] = useState('')
    const [secondNetworkText, setSecondNetworkText] = useState('');
    const [fbAlbumId, setFbAlbumId] = useState(0);
    const [suggestions, setSuggestions] = useState([]);
    const [handles, setHandles] = useState([]);
    const [showDropdown, setShowDropdown] = useState(false);
    const pageContent = useMemo(() => window?.APP_CONTENT?.add_feed ?? {}, []);
    const albumOptions = useMemo(() => pageContent?.albumOptions ?? [], [pageContent]);

    const addFeedReducer = useSelector(state => state.addFeedReducer);
    const { feedObj, autoCompleteList, error, selectedChildAccount} = addFeedReducer
    const { filter, keywords, latLng, selectedCountry, selectedCategory } = feedObj;
    const lists = autoCompleteList;
    networkId = networkId == 28 ? (filterData?.filter == 72 && selectedChildAccount?.Network?.id == 27) ? 27 : 28 : networkId;

    const onInputChange = (e) => {
        if (e.target.name === "secondText") setSecondNetworkText(e.target.value);
        else setNetworkText(e.target.value)
        if (networkId == 27 && selectedCategory && selectedCountry) {
            const input = e?.target?.value ?? "";
            if (!suggestions.length) {
                if (input && selectedCategory && selectedCountry) {
                    const data = {
                        networkId: networkId,
                        country: selectedCountry.value,
                        category: selectedCategory.value,
                        accountId: selectedChildAccount?.Network?.id == 27 ? selectedChildAccount?.ConnectedAccounts?.id || null : null
                    }
                    getTiktokHashtag(data, input);
                }
            } else {
                setHandles(suggestions.filter(suggestion => String(suggestion.hashtagName).startsWith(input)));
            }
        }
        searchInput(e);
    };

    const getTiktokHashtag = useCallback(debounce((data, input) => {
        TIKTOK_HASHTAG_ID_URL(data).then(({ data: { response, responseCode } }) => {
            if (responseCode == 200) {
                setSuggestions(response);
                setHandles(response.filter(suggestion => String(suggestion.hashtagName).startsWith(input)));
            }
        }).catch(err => console.error("Error loading country data:", err));
    }, 500), []);

    const searchInput = useCallback(debounce((e) => {
        const { value, name } = e.target
        const dataKey = name === "secondText" ? "secondText" : "feedText";
        if ((filter == 8 && networkId == 3) || (filter == 3 && networkId == 1) || ([11, 1, 75].includes(parseInt(filter)) && networkId == 7) || (networkId == 19 && filter == 28)) handleAutocomplete(value)
        else dispatch(SET_FEED_OBJECT({ [dataKey]: value }))
    }, 500), [filter, networkId, latLng])

    const onSelectList = (list) => {
        dispatch(SET_SELECTED_LIST(list))
        document.querySelector(`#tw-List`).value = list.name
        setShowList(false)
    }

    const handleAutocomplete = (value) => {
        if (String(value).trim() == '') {
            dispatch(SET_AUTOCOMPLETE_HANDLE([]))
            dispatch(SET_FEED_OBJECT({ feedText: value }))
        } else {
            dispatch(AUTOCOMPLETE_INPUT(value, networkId, filter, latLng))
            dispatch(SET_FEED_OBJECT(networkId == 19 ? { secondText: value } : { feedText: value }));
        }
    }

    const onDropdownSelect = (key, value) => {
        dispatch(SET_FEED_OBJECT({ [key]: value }));
    }

    const onKeywordChange = (e) => {
        const { value } = e.target
        dispatch(SET_FEED_OBJECT({ keywords: value }));
    }

    const onHashtagSelect = (e) => {
        setHandles([]);
        setNetworkText(e?.hashtagName ?? "");
        dispatch(SET_FEED_OBJECT({ feedText: e?.hashtagName ?? "", selectedHashtag: e }));
    }

    useEffect(() => {
        if(networkId == 27){
            setShowDropdown(handles?.length);
            if(!suggestions?.length && (countries?.length || category?.length)){
                setNetworkText('');
            }
        }
    }, [countries, category, networkId, handles])

    useEffect(() => {
        if(filter == 72 && networkId == 28) {
            let selObj = {};
            if(!selectedCountry) selObj = { selectedCountry: { "label": "United States", "value": "US" } }
            if(!selectedCategory) selObj = {...selObj, selectedCategory: { "label": "all", "value": "ALL" } }
            if(selObj) dispatch(SET_FEED_OBJECT(selObj))
        } else setSuggestions([]);
    }, [filter, latLng])

    useEffect(() => {
        setNetworkText('')
        setSecondNetworkText('')
        dispatch(SET_FEED_OBJECT({ feedText: '', feed33: '', field33placeId: '', field33placeName: '', secondText: '' }))
    }, [activeTab])

    useEffect(() => {
        if(networkId == 3 && (filter == 67 || filter == 65) && !fbAlbumId){
            const option = albumOptions[0];
            setFbAlbumId(option.value);
            onDropdownSelect('filter', option.value);
        }
    }, [networkId, filter, fbAlbumId])

    if (networkId == 4) return <div className="form-group"><SearchPlace filterData={filterData} fieldError={error} activeTab={activeTab} /></div>

    return <>
        {/* -----for facebook album tab---------- */}
        {filterData.filter == 67 ?
            <Col sm={5}>
                <InputGroup className="mb-3">
                    <InputGroup.Text id={`network-${networkId}`}>
                        <FontAwesomeIcon type={'solid'} icon={'album'} />
                    </InputGroup.Text>
                    <Select className={`selectpicker selectpicker-lg`} classNamePrefix="selectpicker" options={albumOptions} defaultValue={albumOptions[0]} onChange={(e) => {
                        setFbAlbumId(e.value);
                        onDropdownSelect('filter', e.value);
                    }} />
                </InputGroup>
            </Col> : null}

        {(networkId == 19 && filterData.filter == 28) ? <Col sm={6}>
            <SearchPlace filterData={filterData} fieldError={error} activeTab={activeTab} />
        </Col> : null}

        {(networkId == 27) ? <>
            <Col sm={6}>
                <InputGroup className="mb-2">
                    <InputGroup.Text id={`network-${filterData.filter}`}>
                        <FontAwesomeIcon type={'solid'} icon={'location-dot'} />
                    </InputGroup.Text>
                    <Select className={`selectpicker selectpicker-lg`} classNamePrefix="selectpicker" value={selectedCountry} options={countries} onChange={(e) => onDropdownSelect('selectedCountry', e)} />
                </InputGroup>
            </Col>
            <Col sm={6}>
                <InputGroup className="mb-2">
                    <InputGroup.Text id={`network-${filterData.filter}`}>
                        <FontAwesomeIcon type={'solid'} icon={'layer-group'} />
                    </InputGroup.Text>
                    <Select className={`selectpicker selectpicker-lg`} classNamePrefix="selectpicker" value={selectedCategory} options={category} onChange={(e) => onDropdownSelect('selectedCategory', e)} />
                </InputGroup>
            </Col>
        </> : null}

        {networkId != 20 && <Col sm={[3, 32, 28].includes(filterData.filter) ? 6 : filterData.filter == 67 ? 7 : 12}>
            <div className="form-group">
                {[32].includes(filterData.filter) ?
                    // * for youtube location tab
                    <SearchPlace filterData={filterData} fieldError={error} activeTab={activeTab} />
                    :
                    //* -----------common input field---------- */ }
                    <>
                        <InputGroup className={`mb-3`}>
                            <InputGroup.Text id={`network-${networkId}`}>
                                <FontAwesomeIcon type={'solid'} icon={getInputIcon(filterData.filter, networkId)} />
                            </InputGroup.Text>
                            <Form.Control
                                key={`${networkId}-text`}
                                data-input="network"
                                size='lg'
                                name={(networkId == 19 && filter == 28)? "secondText" : "feedText"}
                                id={`${filterData.feedtype.replace(" ", "-")}-text`}
                                placeholder={getInputPlaceholder(fbAlbumId == 65 ? { filter: 65 } : filterData, networkId)}
                                value={(networkId == 19 && filter == 28) ? secondNetworkText : networkText}
                                onChange={onInputChange}
                                aria-label=""
                                className={`fw-medium ${filterData.filter == activeTab && (error.show || ((networkId == 19 && filter == 28) && error.showSecond)) ? 'is-invalid' : ''}`}
                                aria-describedby={`network-${networkId}`}
                            />
                            {(error.show || ((networkId == 19 && filter == 28) && error.showSecond)) ? <span id="" className="invalid-feedback">{error.message || ""}</span> : null}
                        </InputGroup>
                        {(networkId == 27 && handles?.length) ? (
                            <Dropdown className={`tiktok-dropdown`} autoClose="outside" show={showDropdown} onToggle={() => setShowDropdown(!showDropdown)}>
                                <Dropdown.Menu className='input-dropdown'>
                                    <div className="dropdown-height">
                                        {handles.map((e, index) => (
                                            <Dropdown.Item onClick={() => onHashtagSelect(e)} key={`suggestion-${index}`}>
                                                {e?.hashtagName ?? ""}
                                            </Dropdown.Item>
                                        ))}
                                    </div>
                                </Dropdown.Menu>
                            </Dropdown>
                        ) : null}
                        {((filter == 8 && networkId == 3) || (filter == 3 && networkId == 1) || ([11, 1, 75].includes(parseInt(filter)) && networkId == 7) || (networkId == 19 && filter == 28)) ? <AutoCompleteBlock filterData={filterData} filter={filter} setNetworkText={setNetworkText} setSecondNetworkText={setSecondNetworkText}/> : <></>}

                    </>
                }

            </div>
        </Col>}

        {(networkId == 15 && filterData.filter == 22 || networkId == 12 && filterData.filter == 13) ? <Col sm={12}>
            <InputGroup className="mb-3">
                <InputGroup.Text id={`network-${networkId}`}>
                    <FontAwesomeIcon type={'solid'} icon={networkId == 15 ? "key" : "link"} />
                </InputGroup.Text>
                <Form.Control
                    key={`${networkId}-text`}
                    data-input="network"
                    size='lg'
                    id={`${filterData.feedtype.replace(" ", "-")}-text-second`}
                    placeholder={networkId == 15 ? pageContent?.enter_access_token ?? "" : pageContent?.enter_rss_feed_url ?? ""}
                    name="secondText"
                    value={secondNetworkText}
                    onChange={onInputChange}
                    className={`fw-medium ${(filterData.filter == activeTab && error.showSecond) ? 'is-invalid' : ''}`}
                    aria-describedby={`network-${networkId}`}
                />
                {error.showSecond ? <span id="" className="invalid-feedback">{error.message || ""}</span> : null}
            </InputGroup>
        </Col> : null}

        {/* ------------ input for twitter list tab--------------- */}
        {
            filterData.filter == 3 ?
                <Col sm={6}>
                    <InputGroup className="mb-3">
                        <InputGroup.Text id={`network-${networkId}`}>
                            <FontAwesomeIcon type={'solid'} icon={'list'} />
                        </InputGroup.Text>
                        <Form.Control
                            key={`${networkId}-list`}
                            data-input="network"
                            size='lg'
                            className='fw-medium'
                            placeholder={pageContent?.list_name ?? ""}
                            id="tw-List"
                            onClick={() => setShowList(!showList)}
                        />
                    </InputGroup>
                    <Dropdown className={`w-100`} autoClose="outside" show={showList}>
                        {lists && lists.length ?
                            <Dropdown.Menu className='input-dropdown'>
                                <div className="dropdown-height">
                                    {lists.map(list => (
                                        <Dropdown.Item onClick={() => onSelectList(list)} key={`hhhh_${list.id}`}>
                                            <div className="d-flex algin-items-center">
                                                <div className="d-flex flex-column justify-content-start">
                                                    <p className="fw-bold fs-8 mb-0">{list.name}</p>
                                                </div>
                                            </div>
                                        </Dropdown.Item>
                                    ))}
                                </div>
                            </Dropdown.Menu>
                            : null}
                    </Dropdown>
                    <div>
                    </div>
                </Col>
                : <></>
        }

        {((networkId == 20 && filterData.filter == 1) || [32].includes(filterData.filter)) ?
            <Col sm={6}>
                {/* <SearchPlace onFieldTextChange={onKeywordChange}/> */}
                <InputGroup className="mb-3">
                    <InputGroup.Text id={`network-${networkId}`}>
                        <FontAwesomeIcon type={'solid'} icon={'text'} />
                    </InputGroup.Text>
                    <Form.Control
                        key={`${networkId}-keywords`}
                        data-input="network"
                        size='lg'
                        className='fw-medium'
                        placeholder={pageContent?.enter_keyword ?? ""}
                        defaultValue={keywords || ""}
                        onChange={onKeywordChange}
                    />
                </InputGroup>
            </Col>
            : null
        }
    </>
}

export default TabInputTab