
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { Nav, Tab, Card, Container, Button, Row, Col } from 'react-bootstrap';
import { IS_API_ACCESS_LIVE, MediaBaseUrl, WEBSITE_EMBED_URL } from '../../../../../../actions/api';
import { CREATE_ONSITE_FEED, SNAPUP_DETAILS_ACTION } from '../../../../../../actions/addFeedActions';
import withRouter from '../../../../../routes/WithRouter';
import ShareCodeModal from '../../../CommonComponents/ShareCodeModal';
import CardAside from '../../../CommonComponents/CardAside';
import Aside from './PostSubmitModal/Aside';
import Preview from './PostSubmitModal/Preview';
import Publish from './PostSubmitModal/Publish';
import QRAside from './QRCode/QRAside';
import QRPreview from './QRCode/QRPreview';
import { Loader } from '../../../CommonComponents/Loader';
import { GET_LOCAL, shopifyApp, shopifyForm } from '../../../../../../utils';
import SideMenu from '../../../Moderation/SideMenu';
import DownloadCsv from './DownloadCsv';
import { getOnsiteLangData } from '../../../../../../actions/websiteAction';
import { ONSITE_LANGUAGE } from '../../../../../../actions/constants';
import { SET_SNAP_UP_ONBOARDIN_DTA } from '../../../../../../reducer/keys';
import SubHeaderCrumbs from '../../../CommonComponents/SubHeader';
import './ReviewHub.scss'
import './_snapup.scss'

class SnapUp extends Component {
    state = {
        onSiteEnable: true,
        loading: false,
        title: 'Submit your post',
        subTitle: 'You can share your reviews/rating here',
        error: false,
        subtitleError: false,
        selectedColor: '#eff2f3',
        btnText: 'Submit',
        btnTextError: false,
        msgError: false,
        backgroundImage: '',
        msg: 'The post(s) will be appeared after the admin approval',
        tinyUrl: `${WEBSITE_EMBED_URL}${localStorage.getItem('wallId')}${IS_API_ACCESS_LIVE ? '/onsite-upload?wall=1' : '?onsite-upload'}`,
        uniqueCode: null,
        isLoading: false,
        showModal: false,
        dataShare: null,
        logoImage: null,
        moderation: false,
        ugcSettings: null,
        firstTime: true,
        filterToggle: false,
        error: false,
        buttonText: "Share your experience",
        bgColor: "#ffffff",
        fcColor: "#000000",
        fontSize: 8,
        qrStatus: true,
        publishOption: 'link',
        shareCodeType: 1,
        isReview: true,
        loader: true,
        feedData: null,
        snapLayout: 1,
        previewKey: 'review_modal',
        showEmbed: false,
        isChanged: false,
        showDownloadCsv: false,
        onsiteLanguage: { label: "English", value: "en" },
        formContent: null
    }
    isShopify = shopifyApp()
    pageContent = window?.APP_CONTENT?.snapup ?? {};

    getSnapUpForm = async () => {
        const data = await getOnsiteLangData();
        this.setState({ formContent: data });
    }

    componentWillMount() {
        const { getSnapUp } = this.props;
        this.getSnapUpForm();
        getSnapUp();
        const isPreviousOwner = GET_LOCAL('ownerId') < 1778806;
        if(isPreviousOwner) this.setState({ buttonText: "Scan QR or Tap to submit UGC", bgColor: "#000", fcColor: "#fff"});
    }

    discardFeed = () => {
        shopifyForm('hide')
        this.props.navigate(`/content/addfeed/${localStorage.getItem('wallId')}`)
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.snapLoader != this.props.snapLoader) {
            this.setState({ loader: true })
            if (!this.props.snapLoader) this.onLoadSettings(this.props.snapup_details)
        }
        if ((prevState.isChanged != this.state.isChanged) && this.state.isChanged) shopifyForm()
    }

    componentWillUnmount() {
        const {removeOnSnapDta, snapOnboardingDta} = this.props
        if (this.isShopify) {
            window.removeEventListener('onSaveClicked', this.onSubmit)
            window.removeEventListener('onDiscardClicked', this.discardFeed)
        }
        if(snapOnboardingDta) removeOnSnapDta();
    }

    onLoadSettings = (feed) => {
        const ugcSettings = feed.onsite_settings ? feed.onsite_settings : null
        const onboardingDta = this.props.snapOnboardingDta ?? false
        if (ugcSettings) {
            const { onsite_bg_color_wg, onsite_bg_image_wg, onsite_popup_btn_txt, onsite_popup_sub_heading, onsite_popup_title, onsite_succ_msg, onsite_status, onsite_logo, onsite_snap_layout, onsite_language } = ugcSettings;

            const title = onboardingDta ? onboardingDta?.title : onsite_popup_title
            const logoImage = onboardingDta ? onboardingDta?.logoImage : onsite_logo
            const btnText = onboardingDta ? onboardingDta?.btnText : onsite_popup_btn_txt

            this.setState({ title: title, subTitle: onsite_popup_sub_heading, moderation: feed?.moderation, btnText: btnText, msg: onsite_succ_msg, backgroundImage: onsite_bg_image_wg, logoImage: logoImage, selectedColor: onsite_bg_color_wg, onSiteEnable: onsite_status == 1 ? true : false, buttonText: "Share Your Experience", ugcSettings: ugcSettings, firstTime: feed.id ? false : true, uniqueCode: feed?.value1 ?? "", loader: false, feedData: feed, snapLayout: onsite_snap_layout, onsiteLanguage: ONSITE_LANGUAGE?.find(lang => lang.value == onsite_language) || { label: "English", value: "en" } })
        } else {
            this.setState({ title: 'Submit your post', subTitle: 'You can share your reviews/rating here', moderation: false, btnText: 'Submit', msg: 'The post(s) will be appeared after the admin approval', backgroundImage: '', logoImage: '', selectedColor: '#eff2f3', onSiteEnable: true, buttonText: "Share Your Experience", firstTime: feed?.id ? false : true, loader: false })
        }
    }


    onChangeHandler = event => this.setState({ [event.target.name]: event.target.value, error: false, subtitleError: false, subtitleError: false, btnTextError: false, msgError: false, isChanged: true });

    onCheckHandler = event => { this.setState({ [event.target.name]: event.target.checked, snapLayout: this.state.isReview ? 1 : 2, isChanged: true }); }

    onColorChange = (value) => this.setState({ selectedColor: value })

    onClickTouploadImage = (value, callBack) => this.setState({ backgroundImage: value, isChanged: true }, () => callBack())

    onClickTouploadLogo = (value, callBack) => this.setState({ logoImage: value, isChanged: true }, () => callBack())

    shareEmbedCallback = (shareEmbedEvent) => this.setState({ showModal: shareEmbedEvent, dataShare: null })

    onClickToOpen = (dataShare, shareCodeType) => event => this.setState({ showModal: true, dataShare, shareCodeType })

    closeAside = () => this.setState({ filterToggle: false })

    onFontSize = event => this.setState({ fontSize: parseInt(event > 30 ? 30 : event) })

    onQrStatus = event => this.setState({ qrStatus: event.target.checked })

    onChangeText = event => this.setState({ buttonText: event.target.value })

    publishOption = e => this.setState({ publishOption: e })

    onChangeModeration = () => this.setState({ moderation: !this.state.moderation, isChanged: true })

    onSubmit = () => {
        const { title, subTitle, backgroundImage, selectedColor, onSiteEnable, btnText, msg, logoImage, moderation, feedData, snapLayout, firstTime, onsiteLanguage } = this.state;

        if (title == '') this.setState({ error: true });
        else if (subTitle == '') this.setState({ subTitle: true });
        else if (btnText == '') this.setState({ btnTextError: true });
        else if (msg == '') this.setState({ msg: true });
        else {
            var obj = {
                network: 29,
                filter: 2,
                moderation: moderation ? 1 : 0,
                onsite_popup_title: title,
                onsite_popup_btn_txt: btnText,
                onsite_popup_sub_heading: subTitle,
                onsite_term_url: "https://taggbox.com/on-site-uploads-content-rights/",
                onsite_logo: logoImage ? logoImage : '',
                onsite_bg_color_wg: selectedColor,
                onsite_bg_image_wg: backgroundImage ? backgroundImage : '',
                onsite_succ_msg: msg,
                onsite_status: onSiteEnable ? 1 : 0,
                wallId: localStorage.getItem('wallId'),
                onsite_snap_layout: snapLayout,
                onsite_qr_code_status: onSiteEnable ? 1 : 0,
                onsite_language: onsiteLanguage?.value || "en"
            };
            if (feedData?.onsite_settings) obj = { ...obj, id: feedData.id }
            else {
                const isPreviousOwner = GET_LOCAL('ownerId') < 1778806;
                obj = {
                    ...obj,
                    onsite_qr_code_btn_text: isPreviousOwner ? "Scan QR or Tap to submit UGC" : "Share your experience",
                    onsite_btn_color: isPreviousOwner ? "#000" : "#ffffff",
                    onsite_btn_txt_color: isPreviousOwner ? "#fff" : "#000000",
                    onsite_link_font_btn: "Default",
                    onsite_css_font_btn: "Default",
                    onsite_font_varient_btn: "",
                    btn_fontSize: 16,
                    onsite_button_status: 1,
                    onsite_thank_txt_color: "#000000",
                    onsite_link_font_msg: "Default",
                    onsite_css_font_msg: "Default",
                    onsite_font_varient_msg: "",
                    onsite_thank_fontSize: 16,
                    choose_content_category: 1,
                }
            }
            this.setState({ loading: true, error: false, subtitleError: false, btnTextError: false, msgError: false });
            CREATE_ONSITE_FEED(obj, () => this.setState({ loading: false, isChanged: false }, () => shopifyForm('hide')), this.props.navigate, firstTime ? true : false)
        }

    }

    changeUgcTab = () => {
        this.setState({ isReview: !this.state.isReview })
    }

    onShowEmbed = () => {
        this.setState({ showEmbed: !this.state.showEmbed })
    }

    render() {
        const { loading, shareCodeType, showModal, firstTime, dataShare, filterToggle, isReview, loader, previewKey, showDownloadCsv } = this.state;
        const wallId = localStorage.getItem('wallId');
        const wallData = this.props.allWalls?.find((e) => e.id == localStorage.getItem('wallId')) ?? {};
        const breadCrumbs = window?.APP_CONTENT?.['sideBar'];
        return <>
            {wallData?.id ? <SubHeaderCrumbs key={wallData?.id} crumbs={[{ name: wallData?.name ?? "", icon: "grid-2-plus", imgUrl: wallData?.thumbnail ?? "" }, { name: breadCrumbs?.home, link: "/home" }, { name: breadCrumbs?.contentGallery, link: "/content" }, { name: wallData?.name, link: `/content/moderation/${wallData?.id}` }, { name: breadCrumbs?.reviewHub, link: `/content/addfeed/${wallData?.id}/29` }]} showGallerySwitcher={false} /> : null}
            <div className={`custom-side-menu d-flex`}>
                <SideMenu {...{ getWallData: wallData || { id: wallId } }} />
                <div className="custom-main-content">
                    <div className="subheader">
                        <Container fluid>
                            <div className='f-between'>
                                <div className="nav nav-underline">
                                    <div className='nav-item'>
                                        <button className={`nav-link ${isReview ? 'active' : ''}`} onClick={this.changeUgcTab} disabled={loader}>
                                            <FontAwesomeIcon icon={'star'} iconClass="me-1" />
                                            {this.pageContent?.review ?? ""}
                                        </button>
                                    </div>
                                    <div className='nav-item'>
                                        <button className={`nav-link ${!isReview ? 'active' : ''}`} onClick={this.changeUgcTab} disabled={loader}>
                                            <FontAwesomeIcon icon={'image'} iconClass="me-1" />
                                            {this.pageContent?.visual_ugc ?? ""}
                                        </button>
                                    </div>
                                    <div className='nav-item'>
                                        <button className={`nav-link`} onClick={() => this.setState({ showDownloadCsv: true })}>
                                            <FontAwesomeIcon type={'solid'} icon={'download'} iconClass="me-1"/>
                                            {this.pageContent?.download_data ?? ""}
                                        </button>
                                    </div>
                                </div>

                                <Button id="toggle_aside" variant='link' size='sm' className={`d-flex ${this.isShopify ? 'd-md-none' : 'd-xl-none'}`} onClick={() => this.setState({ filterToggle: !filterToggle })}>
                                    {this.pageContent?.menu ?? ""}
                                    <FontAwesomeIcon icon={'sliders'} iconClass={'ms-2 lh-base'} />
                                </Button>
                            </div>
                        </Container>
                    </div>
                    <div className={`d-flex flex-column flex-column-fluid full-content`}>
                        <Container className="container-reivewhub container-reivewhub-app" fluid>
                            {!loader ? <Tab.Container defaultActiveKey={'review_modal'} className="w-100" onSelect={event => this.setState({ previewKey: event })}>
                                <Card>
                                    <div onClick={() => this.props.navigate(`/content/moderation/${wallId}`)} style={{ right: '15px', top: '10px', zIndex: '9', height: '20px', width: '20px', display: 'flex', alignItems: 'center', justifyContent: 'center' }} className="position-absolute cursor-pointer rounded-2">
                                        <FontAwesomeIcon icon="xmark" iconClass="cursor-pointer fs-3" />
                                    </div>
                                    <Card.Body className='p-0'>
                                        <div className="p-0 d-flex">
                                            <CardAside filterToggle={filterToggle} closeAside={this.closeAside}>
                                                <Card className='a_footer_ a_header_'>
                                                    <Card.Header>
                                                        <Nav variant="underline" className='justify-content-center' style={{ marginTop: 6 }}>
                                                            <Nav.Item className='me-2'>
                                                                <Nav.Link eventKey="review_modal">{this.pageContent?.form ?? ""}</Nav.Link>
                                                            </Nav.Item>
                                                            <Nav.Item className='me-2'>
                                                                <Nav.Link eventKey="qr_code">{this.pageContent?.qr_code ?? ""}</Nav.Link>
                                                            </Nav.Item>
                                                        </Nav>
                                                    </Card.Header>
                                                    <Card.Body className='aside_body p-0'>
                                                        <div className='p-4 mb-4'>
                                                            <Tab.Content>
                                                                <Tab.Pane eventKey="review_modal">
                                                                    <Aside onCheckHandler={this.onCheckHandler} onChangeHandler={this.onChangeHandler} onClickTouploadLogo={this.onClickTouploadLogo} changeColor={(clr) => this.setState({ selectedColor: clr, isChanged: true })} onClickTouploadImage={this.onClickTouploadImage} {...this.state} onChangeModeration={this.onChangeModeration} pageContent={this.pageContent} ONSITE_LANGUAGE={ONSITE_LANGUAGE} />
                                                                </Tab.Pane>
                                                                <Tab.Pane eventKey="qr_code">
                                                                    <QRAside onClickToOpen={this.onClickToOpen} onChangeText={this.onChangeText} onBgColorChange={(clr) => this.setState({ bgColor: clr })} onFcColorChange={(clr) => this.setState({ fcColor: clr })} onQrStatus={this.onQrStatus} onFontSize={this.onFontSize}  {...this.state} pageContent={this.pageContent} />
                                                                </Tab.Pane>
                                                            </Tab.Content>
                                                        </div>
                                                    </Card.Body>
                                                    <Card.Footer className='d-flex justify-content-between py-3'>
                                                        <Button variant='link' className='me-2 text-nowarp' id="backtofeed" onClick={() => this.props.navigate(`/content/addfeed/${wallId}`)} >
                                                            <FontAwesomeIcon icon={'arrow-left'} iconClass="me-1" />
                                                            {this.pageContent?.back ?? ""}</Button>
                                                        <Button className={`${loading ? 'spinner' : ""}`} id="create_feed" onClick={() => previewKey == 'qr_code' ? this.onShowEmbed() : this.onSubmit()}>{previewKey == 'qr_code' ? this.pageContent?.generate_code ?? "" : firstTime ? this.pageContent?.create_feed ?? "" : this.pageContent?.update_feed ?? ""}</Button>
                                                    </Card.Footer>
                                                </Card>
                                            </CardAside>
                                            <Card className="preview_">
                                                <Card.Body>
                                                    <div className='bg-gray-100 rounded-3'>
                                                        <Row>
                                                            <Col lg={{ order: 2, span: 5 }}>
                                                                <Publish {...this.state} onShowEmbed={this.onShowEmbed} pageContent={this.pageContent} />
                                                            </Col>
                                                            <Col lg={7}>
                                                                <Tab.Content className='w-100'>
                                                                    <Tab.Pane eventKey="review_modal">
                                                                        <Preview {...this.state} onClickToOpen={this.onClickToOpen} />
                                                                    </Tab.Pane>
                                                                    <Tab.Pane eventKey="qr_code">
                                                                        <QRPreview {...this.state} onClickToOpen={this.onClickToOpen} />
                                                                    </Tab.Pane>
                                                                </Tab.Content>
                                                            </Col>
                                                        </Row>
                                                    </div>
                                                </Card.Body>
                                            </Card>
                                        </div>
                                        {showModal ?
                                            <ShareCodeModal type={6} from={'snapUp'} onClose={(value) => this.setState({ showModal: value })} sharecode={{ 'emailCmp': dataShare }} shareUrl={dataShare} isShare={shareCodeType == 2 ? false : true} codeId={'#buttonEmbedCode'} />
                                            : <></>}
                                    </Card.Body>
                                </Card>
                            </Tab.Container> :

                                <div className='f-center' style={{ minHeight: 'calc(100vh - 170px)' }}><Loader bg={'body'} /></div>}
                        </Container>
                    </div>
                </div>
                {showDownloadCsv ? <DownloadCsv show={showDownloadCsv} hideModal={() => this.setState({ showDownloadCsv: false })} wallId={wallId} pageContent={this.pageContent} /> : null}
            </div>
        </>
    }
}
const mapStateToProps = (state) => {
    return {
        snapup_details: state.addFeedReducer?.details,
        snapLoader: state.addFeedReducer?.snapUpLoad,
        snapOnboardingDta: state.addFeedReducer?.snapOnData,
        allWalls: state?.dashboardData?.dashboarddata?.allWalls ?? []
    }
}

const mapDispatchToProps = dispatch => {
    return {
        getSnapUp: () => dispatch(SNAPUP_DETAILS_ACTION()),
        removeOnSnapDta: () => dispatch({type:SET_SNAP_UP_ONBOARDIN_DTA, payload:null})
    }
}


export default connect(mapStateToProps, mapDispatchToProps)(withRouter(SnapUp))