import React, { useEffect, useState } from 'react'
import { Tab, Tabs, Nav } from 'react-bootstrap'
import TabBody from './TabBody'
import { useDispatch, useSelector } from 'react-redux'
import { SET_FEED_OBJECT } from '../../../../../../../actions/addFeedActions'
import { RESET_AUTOCOMPLETE } from '../../utils'
import { GET_LOCAL, isPersInstaAcc, parseNumber } from '../../../../../../../utils'
import axios from 'axios'
import { MediaBaseUrl } from '../../../../../../../actions/api'

const TabInput = ({ networkDetails, networkId, filterDenyMsg }) => {
    const dispatch = useDispatch()

    const [activeTab, setActiveTab] = useState(networkDetails?.networkTab ? networkDetails.networkTab[0].filter : null)
    const { personalAccount, connectedAcc } = useSelector(state => state.addFeedReducer)
    const [userAccounts, setUserAccounts] = useState(null)
    const [countries, setCountries] = useState([]);
    const [category, setCategory] = useState([]);
    const [fileName, setFileName] = useState('');


    const getCountries = () => {
        axios.get(`${MediaBaseUrl}media/countries.json`).then(res =>
            setCountries(res.data)
        ).catch(err => console.error("Error loading country data:", err));
    }

    const getCategory = () => {
        axios.get(`${MediaBaseUrl}media/tiktok_hashtag.json`).then(res =>
            setCategory(res.data)
        ).catch(err => console.error("Error loading category data:", err));
    };

    useEffect(() => {
        if ((isPersInstaAcc(parseInt(activeTab)) && networkId == 18) || (networkId == 28 && activeTab == 72)) setUserAccounts(personalAccount)
        else setUserAccounts(connectedAcc)
        if (!activeTab && networkDetails?.networkTab) setActiveTab(networkDetails.networkTab[0].filter)
        dispatch(RESET_AUTOCOMPLETE())
    }, [activeTab, connectedAcc, personalAccount, networkId, networkDetails])

    useEffect(() => setActiveTab(null), [networkId, networkDetails])
    useEffect(() => { if (!activeTab && networkDetails?.networkTab) setActiveTab(networkDetails.networkTab[0].filter) }, [networkDetails])

    //   ------------update filter on tab change---------
    useEffect(() => { 
        if (networkId == 28 && activeTab == 72 && !countries?.length && !category?.length) {
            getCategory();
            getCountries()
        } 
        dispatch(SET_FEED_OBJECT({ filter: activeTab, feedText: '', secondText: '' }));
    }, [activeTab, dispatch, networkDetails]);

    const ownerId = GET_LOCAL("ownerId");

    if (!networkDetails.loading && networkDetails?.networkTab) {
        return <Tab.Container key={`${networkId}_${activeTab}`} activeKey={activeTab} defaultActiveKey={networkDetails.networkTab[0].filter} variant='underline' className="mb-5" onSelect={e => setActiveTab(parseNumber(e))}>
            <Nav variant="underline" className="mb-5 border-bottom pb-3 pb-sm-2 pb-md-0 pb-lg-0 pb-xl-0 lh-1 lh-sm-1 lh-md-normal lh-xl-normal lh-lg-normal">
                {networkDetails.networkTab.map((filter, index) => ([1609995, 157165].includes(ownerId) && networkId == 3 && filter.filter == 2) ? null : <Nav.Item key={index}>
                    {/* <Nav.Link className={filter.feedName} eventKey={filter.filter}>{filter.feedName}</Nav.Link> */}
                    <Nav.Link data-name={filter.feedName.toLowerCase().replace(/[^a-z0-9]/g, '')} eventKey={filter.filter}>{filter.feedName}</Nav.Link>
                </Nav.Item>)}
            </Nav>
            <Tab.Content key={networkId}>
                {networkDetails.networkTab.map(filter => <Tab.Pane eventKey={filter.filter} key={filter.filter}>
                    <TabBody {...{ fileName, setFileName, networkDetails, activeTab, countries, category, filterDenyMsg, filterData: filter, accounts: userAccounts }}/>
                </Tab.Pane>
                )}
            </Tab.Content>
        </Tab.Container>

    }
    return <></>
}

export default TabInput