import React, { useEffect, useMemo } from 'react';
import { useParams } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { GET_NETWORK_INFO_BY_ID, FETCH_CONNECTED_ACCOUNTS, GET_ADD_FEED } from '../../../../../actions/moderationActions';
import { SET_FEED_LOADING, SET_FEED_OBJECT } from '../../../../../actions/addFeedActions';
import { GET_NETWORK_DATA_BY_ID } from '../../../../../utils';
import SnapUp from './SnapUp';
import FeedBody from './FeedBlock/FeedBody';
import { Loader } from '../../../Main/CommonComponents/Loader';
import { GET_FEEDS } from '../../../../../actions/manageFeedsAction';

const CreateFeed = () => {
    const { networkId } = useParams();
    const dispatch = useDispatch();
    const { networkDetails, addFeedNetworks, details } = useSelector(state => state.addFeedReducer);
    const feeds = useSelector(state => state?.manageFeeds?.feeds);
    
    useEffect(() => {
        if(!feeds) dispatch(GET_FEEDS());
        dispatch(GET_ADD_FEED());
        dispatch(FETCH_CONNECTED_ACCOUNTS(networkId));
        dispatch(SET_FEED_LOADING(false));
        dispatch(GET_NETWORK_INFO_BY_ID(parseInt(networkId)));
        dispatch(SET_FEED_OBJECT({ moderation: 0, feedText: "", filter: 0, keywords: "", secondText: "", latLng: "", excludeRetweet: 0, excludeReplies: 0, excludeRetweet: 0, feed33: "", field33placeId: "", field33placeName: "", connetedAccount: 0, account: 0, hashtagIGTV: 0, onlyReels: 0 }));
        if (networkId === '18') dispatch(FETCH_CONNECTED_ACCOUNTS(2));
        if(networkId == 28) dispatch(FETCH_CONNECTED_ACCOUNTS(27));
    }, [networkId, dispatch]);

    useEffect(() => {
        // Check if networkDetails.id is null/undefined and addFeedNetworks is available
        if (!networkDetails?.id && addFeedNetworks?.networks?.length) {
            dispatch(GET_NETWORK_INFO_BY_ID(parseInt(networkId)));
        }
    }, [addFeedNetworks, networkDetails]);

    const network = useMemo(() => GET_NETWORK_DATA_BY_ID(networkId), [networkId, addFeedNetworks]);

    return (networkDetails?.id ? <>
                {networkId != 29 ? <FeedBody networkId={networkId} network={network} networkDetails={networkDetails} /> : (details && networkDetails?.networkTab) ? <SnapUp /> : null}
            </> : <div className='f-center' style={{ minHeight: 'calc(100vh - 100px)' }}><Loader /></div>
        );
};

export default CreateFeed;