import { Form, Dropdown, Image } from 'react-bootstrap';
import ColorPicker from '../../../../CommonComponents/ColorPicker';
import AttachImage from '../../../../CommonComponents/Uploads/AttachImage';
import { MediaBaseUrl } from '../../../../../../../actions/api';

const Aside = ({onCheckHandler, onChangeHandler, onClickTouploadLogo, changeColor, onClickTouploadImage,  onSiteEnable, title,subtitleError, subTitle,  logoImage, selectedColor, backgroundImage, btnTextError, btnText, msg, error, msgError, isReview, snapLayout, moderation, onChangeModeration, pageContent, onsiteLanguage, ONSITE_LANGUAGE }) => {

    return (<>
        <Form.Group className='mb-3'>
            <Form.Check type="switch" id="enable_su" className="f-between" label={pageContent?.enable ?? ""} name="onSiteEnable" checked={isReview ? snapLayout==1 &&onSiteEnable : snapLayout == 2 && onSiteEnable} onChange={(e) => onCheckHandler(e)} reverse />
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Label className="mb-2" htmlFor="su_title">{pageContent?.title_label ?? ""} <span className="text-danger ms-1">*</span></Form.Label>
            <Form.Control id="su_title" name="title" className={error ? 'is-invalid' : ''} placeholder={pageContent?.title_ph ?? ""} value={title} onChange={(e) => onChangeHandler(e)} />
            {error ? <span className="invalid-feedback">{pageContent?.field_req ?? ""}</span> : null}
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Label className="mb-2" htmlFor="su_sub_title">{pageContent?.sub_title_label ?? ""}</Form.Label>
            <Form.Control as="textarea" name="subTitle" id="su_sub_title" className={subtitleError ? 'is-invalid' : ''} placeholder={pageContent?.sub_title_ph ?? ""} value={subTitle} onChange={(e) => onChangeHandler(e)} />
            {subtitleError ? <span className="invalid-feedback">{pageContent?.field_req ?? ""}</span> : null}
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Label className="mb-2" htmlFor="su_language">{pageContent?.language_label ?? ""}</Form.Label>
            <Dropdown className="w-100" id="su_language" >
                <Dropdown.Toggle className="text-capitalize text-start w-100 border position-relative" variant="" id="dropdown-basic">
                    <Image className='me-2 border' src={`${MediaBaseUrl}media/images/flags/${onsiteLanguage?.value || "en"}.svg`} height={17} width={28} fluid /> {onsiteLanguage?.label || "English"}
                </Dropdown.Toggle>
                <Dropdown.Menu>
                    {ONSITE_LANGUAGE.map((lang) => {
                        return <Dropdown.Item className="text-capitalize" onClick={(e) => onChangeHandler({ target: { name: "onsiteLanguage", value: lang }})}><Image className='me-2 object-fit-contain border' src={`${MediaBaseUrl}media/images/flags/${lang.value}.svg`} height={17} width={28} fluid />{lang.label}</Dropdown.Item>
                    })}
                </Dropdown.Menu>
            </Dropdown>
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Label className="mb-2" htmlFor="su_logo">{pageContent?.logo_label ?? ""}</Form.Label>
            <AttachImage id="su_logo" imageStyle={{ paddingBottom: '50%' }} backgroundImage={logoImage} onClickTouploadImage={onClickTouploadLogo} autoProceed={true} uploadPath={`tmp/`} />
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Label className="mb-2" htmlFor="backgroundColor">{pageContent?.bg_color_label ?? ""}</Form.Label>
            <ColorPicker id="backgroundColor" defaultColor={selectedColor} changeColor={changeColor} />
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Label className="mb-2" htmlFor="su_bg_">{pageContent?.bg_img_label ?? ""}</Form.Label>
            <AttachImage id="su_bg_" imageStyle={{ paddingBottom: '66%' }} backgroundImage={backgroundImage} onClickTouploadImage={onClickTouploadImage} autoProceed={true} />
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Label className="mb-2" htmlFor="su_btn_title">{pageContent?.submit_btn_label ?? ""}</Form.Label>
            <Form.Control id="su_btn_title" name="btnText" className={btnTextError ? 'is-invalid' : ''} placeholder={pageContent?.submit_btn_ph ?? ""} value={btnText} onChange={(e) => onChangeHandler(e)} />
            {btnTextError ? <span className="invalid-feedback">{pageContent?.field_req ?? ""}</span> : null}
        </Form.Group>
        <Form.Group className='mb-4'>
            <Form.Label className="mb-2" htmlFor="ty_msg_">{pageContent?.ty_msg_label ?? ""}</Form.Label>
            <Form.Control as="textarea" name="msg" id="ty_msg_" className={`${msgError ? 'is-invalid' : ''} textarea-min-row-5`} row={5} value={msg} onChange={(e) => onChangeHandler(e)} />
            {msgError ? <span className="invalid-feedback">{pageContent?.field_req ?? ""}</span> : null}
        </Form.Group>
        <Form.Group className='mb-3'>
            <Form.Check label={pageContent?.moderation_label ?? ""} type="checkbox" name='moderation' id='moderation' inline checked={moderation} onChange={onChangeModeration}/>
            </Form.Group>
        </>
    )
}

export default Aside