import React, { useCallback } from 'react';
import { Button, Image, Modal } from 'react-bootstrap';
import { MediaBaseUrl, REVIEW_HUB_DOWNLOAD_CSV } from '../../../../../../actions/api';
import { useApiRequest } from '../../../../../../hooks/useApiRequest';
import { downloadJSONAsCSV } from '../../../../../../utils';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';

const DownloadCsv = ({ show, hideModal, wallId, pageContent }) => {
        const { request, loader } = useApiRequest();
    
    const downloadCsv = useCallback(() => {
        if(wallId){
            request({
                url: REVIEW_HUB_DOWNLOAD_CSV,
                method: "POST",
                body: { wallId },
                onSuccess: (response) => {
                    if(response?.responseData?.length) downloadJSONAsCSV(response?.responseData ?? []);
                    setTimeout(hideModal, 500);
                },
            })
        }
    }, [wallId]);

    return (
        <Modal show={show} onHide={hideModal} keyboard={false} size={'md'} centered>
            <Modal.Body className="text-center p-7">
                <Image className="my-7" src={`${MediaBaseUrl}media/icons/color-icons/download.svg`} height={"auto"} width={400} fluid />
                <p className="text-center my-5 fs-6 mx-auto text-muted" style={{ maxWidth: 400 }}>
                {pageContent?.downlod_desc ?? ""}
                </p>
                <Button className={`${loader ? 'spinner' : ''}`} onClick={downloadCsv}>
                    {pageContent?.button_download ?? ""}
                    <FontAwesomeIcon icon={'arrow-down-to-line'} iconClass="ms-1 me-0"/>
                </Button>
            </Modal.Body>
        </Modal>
    );
}
export default DownloadCsv;