import { MediaBaseUrl } from '../../../../../../actions/api';
import { TOAST_ERROR } from '../../../../../../utils';
import Uploader from '../../../CommonComponents/Uploader';
import { useDispatch } from 'react-redux';
import { SET_FEED_OBJECT } from '../../../../../../actions/addFeedActions';

const uploadMethods = [
    {
        key: "restriction-failed",
        callback: (file, error) => {
            const fileType = file.type.split('/');
            if (!fileType.includes('csv')) {
                TOAST_ERROR("You can only upload csv File")
                return false;
            }
            if (file.size > 5000000) {
                TOAST_ERROR("Maximum allowed size of 4.8 MB")
                return false;
            }
        }
    }
]

const DropZone = ({ fileName, setFileName }) => {
    const dispatch = useDispatch();
    
    const onImgUpload = (uploadedUrl, file) => {
        setFileName(file?.fileName ?? "")
        dispatch(SET_FEED_OBJECT({ secondText: uploadedUrl }));
    }

    const { sub_heading1, sub_heading2, sub_heading3 } = window.APP_CONTENT?.product_catalog?.pop_import ?? {};

    return <div className="bulkURL-box mb-2">
            <p>{sub_heading1} <a href={`${MediaBaseUrl}media/sample/csv-sample.csv?v=1`} target="_self" className="text-link">{sub_heading2}</a> {sub_heading3}</p>
            <Uploader proceed={true} uploadPath={'media/tmpimages/'} allowedFile={['.csv', 'text/csv']} uploadMethods={uploadMethods} onImgUpload={onImgUpload} isModal={false} showFile={true} disInfo={true} height={240} />
            <div className="selected-file">{fileName}</div>
        </div>
}

export default DropZone