import React, { useEffect, useMemo, useState } from 'react'
import AddAccountBlock from '../../../AddFeed/component/AddAccountBlock';
import { Dropdown, Image, Button, OverlayTrigger, Tooltip } from 'react-bootstrap';
import { FontAwesomeIcon, baseUrl } from '../../../../../../../Assets/AssetsHelper';
import { GET_CHILD_ACCOUNTS } from '../../utils';
import { useParams } from 'react-router-dom';
import { GET_NETWORK_DATA_BY_ID, showAddAccounntAlert, GET_FIRST_LETTER, isPersInstaAcc } from '../../../../../../../utils';
import { useDispatch, useSelector } from 'react-redux';
import { SET_SELECTED_ACCOUNT } from '../../../../../../../actions/addFeedActions';

const TabAccountList = ({ accounts, activeTab, filterData }) => {

    const { networkId } = useParams()
    const dispatch = useDispatch()
    const { selectedChildAccount, feedObj } = useSelector(state => state.addFeedReducer)
    const [childAccount, setChildAccount] = useState(null);
    const pageContent = useMemo(() => window?.APP_CONTENT?.add_feed ?? {}, [])

    useEffect(() => {
        setChildAccount(GET_CHILD_ACCOUNTS(accounts, (networkId == 28 && feedObj.filter == 72) ? 27 : networkId, feedObj.filter))
    }, [accounts, networkId, feedObj.filter])

    useEffect(() => { if (childAccount) onSelectAccount(childAccount[0]) }, [childAccount])

    const onSelectAccount = (account) => {
        dispatch(SET_SELECTED_ACCOUNT(account, true))
    }

    const addAccount = () => {
        const network = GET_NETWORK_DATA_BY_ID(networkId) || {};
        localStorage.setItem("connectionType", 'addFeed');
        if (network.id == 18 && isPersInstaAcc(parseInt(activeTab))) {
            showAddAccounntAlert({ ...network, id: 2, networkId: 2 }, false, false);
        } else if(networkId == 28 && activeTab == 72) {
            showAddAccounntAlert({ ...network, id: 27, networkId: 27, name: "TikTok Bussiness" }, false, false);
        } else showAddAccounntAlert(network, false, false);
    }

    return (
        <div className={`${filterData.filter == 3 ? "col-md-6" : "col-md-12"}`}>
            {(accounts?.length && selectedChildAccount) ? <Dropdown className="w-100 connected_acc__">
                <Dropdown.Toggle data-id="connect_btn" variant="secondary" className="d-flex align-items-center w-100 justify-content-between p-2">
                    <div className="d-flex align-items-center flex-grow-1">
                        <div className="symbol symbol-30 rounded-circle flex-shrink-0 me-2 overflow-hidden">
                            {selectedChildAccount?.ConnectedAccounts?.profilePicture ?
                                <Image src={selectedChildAccount?.ConnectedAccounts?.profilePicture} height="44" width="44" alt={"Profile"} fluid onError={event => event.target.src = baseUrl(`media/images/error/no-profile.jpg`)} />
                                : <span className="symbol-label fs-8">{GET_FIRST_LETTER(selectedChildAccount?.ConnectedAccounts?.value2)}</span>
                            }
                        </div>
                        <div className="overflow-hidden w-100 text-start d-flex flex-column" style={{ maxWidth: 300 }}>
                            <p className=" fw-semibold mb-0 text-ellipsis fs-8">{[18, 20, 3, 10].includes(parseInt(networkId)) ? (selectedChildAccount?.ConnectedAccounts.value2 || selectedChildAccount?.ConnectedAccounts.accountName || "") : selectedChildAccount?.ConnectedAccounts.accountName || selectedChildAccount?.ConnectedAccounts.value2 || ""}</p>
                        </div>
                    </div>
                </Dropdown.Toggle>
                <Dropdown.Menu className="w-100">
                    <p className='text-muted fw-medium px-3 my-2 fs-8'>{pageContent?.selectAccountLabel ?? ""}</p>
                    {childAccount?.length ? childAccount.map((acc, index) => (
                        <Dropdown.Item data-item="connected" eventKey={acc?.ConnectedAccounts?.id} key={index} onClick={() => onSelectAccount(acc)} className={`mb-2`}>
                            <div className="d-flex align-items-center">
                                <div className="symbol symbol-30 rounded-circle flex-shrink-0 me-2 overflow-hidden">
                                    {acc.ConnectedAccounts.profilePicture ?
                                        <Image src={acc.ConnectedAccounts.profilePicture} height="44" width="44" alt={`profile - ${index}`} fluid onError={event => event.target.src = baseUrl(`media/images/error/no-profile.jpg`)} />
                                        :
                                        <span className="symbol-label fs-8">{GET_FIRST_LETTER(acc.ConnectedAccounts.accountName || acc.ConnectedAccounts.value2)}</span>
                                    }
                                </div>
                                <div className="overflow-hidden w-100 text-start d-flex flex-column" style={{ maxWidth: 300 }}>
                                    <p className=" fw-semibold mb-0 text-ellipsis fs-8">{[18, 20, 3, 10].includes(parseInt(networkId)) ? acc?.ConnectedAccounts?.value2 || acc?.ConnectedAccounts?.accountName || "" : acc?.ConnectedAccounts?.accountName || acc?.ConnectedAccounts?.value2 || ""}</p>
                                </div>
                                <div>
                                </div>
                            </div>
                        </Dropdown.Item>
                    )) : null}
                    {networkId != 20 ? <Dropdown.Item data-item="connect-new" className="navi-item f-between" onClick={addAccount}>
                        <div className='d-flex align-items-center w-100'>
                            <div className='f-center border p-1 h-34px w-34px border-dashed me-2 rounded-circle border-gray'>
                                <FontAwesomeIcon type="solid" icon={'user'} iconClass={'me-0'} />
                            </div>
                            <div className="fw-bold">{pageContent?.addAnotherAccount ?? ""}</div>
                        </div>
                        <FontAwesomeIcon type="solid" icon={'plus'} />
                    </Dropdown.Item> : null}
                </Dropdown.Menu>
            </Dropdown> : <AddAccountBlock network={(networkId == 28 && filterData.filter == 72) ? { id: 27, name: "TikTok Business" } : GET_NETWORK_DATA_BY_ID(networkId)} activeTab={activeTab ? activeTab : 0} />}
        </div>
    )
}

export default TabAccountList