import { useSelector } from 'react-redux'
import { Image } from 'react-bootstrap'
import { getFilterName } from '../utils'
import { baseUrl } from '../../../../../../Assets/AssetsHelper'
import { formatTemplateText, GET_NETWORK_ICON, isPersInstaAcc } from '../../../../../../utils'

const FeedTitle = ({ network, pageContent }) => {
    const { filter } = useSelector(state => state.addFeedReducer.feedObj);
    return <div className="d-flex flex-wrap align-items-center pb-5">
        <div className="symbol symbol-50 flex-shrink-0 me-4">
            <Image src={baseUrl(`media/icons/social-icons-square/${GET_NETWORK_ICON(network?.id)}.svg`)} fluid />
        </div>
        <div className="d-flex flex-column flex-grow-1 my-lg-0 my-2 pe-3">
            <h3 className="mb-1">{network?.name}</h3>
            <p className="text-muted mb-0 fw-medium">{formatTemplateText(pageContent?.add_network ?? "", { networkName: `${network?.name} ${network?.id == 18 ? isPersInstaAcc(parseInt(filter)) ? "Personal " : "Bussiness " : ""}${network?.id == 12 ? "" : getFilterName(filter) || ""}` })}</p>
        </div>
    </div>
}

export default FeedTitle