import { Form } from 'react-bootstrap';
import ColorPicker from '../../../../CommonComponents/ColorPicker';
import RangeInput from '../../../../CommonComponents/RangeInput';




const QRAside = ({ publishOption, onClickToOpen, onChangeText, onFontSize, onQrStatus, uniqueCode, bgColor,onBgColorChange, onFcColorChange, fcColor, fontSize, qrStatus, tinyUrl, buttonText, error, pageContent }) => {

    return (
        <>
                <Form.Group className='mb-3'>
                    <Form.Label className="mb-2" htmlFor="btn_text_">{pageContent?.btn_text_label ?? ""}</Form.Label>
                    <Form.Control id="btn_text_" name="title" className={error ? 'is-invalid' : ''} placeholder={pageContent?.btn_text_ph ?? ""} value={buttonText} onChange={onChangeText} />
                    {error ? <span className="invalid-feedback">{pageContent?.field_req ?? ""}</span> : null}
                </Form.Group>

                <Form.Group className='mb-3'>
                    <Form.Label className="mb-2" htmlFor="btn_bg_color">{pageContent?.btn_bg_color_label ?? ""}</Form.Label>
                    <ColorPicker id="webBackgroundColor" defaultColor={bgColor} changeColor={onBgColorChange} />
                </Form.Group>

                <Form.Group className='mb-3'>
                    <Form.Label className="mb-2" htmlFor="btn_text_color">{pageContent?.btn_text_color_label ?? ""}</Form.Label>
                    <ColorPicker id="webFontColor" defaultColor={fcColor} changeColor={onFcColorChange} />
                </Form.Group>

                <Form.Group className='mb-3'>
                    <Form.Label className="mb-2" htmlFor="btn_fs_">{pageContent?.btn_fs_label ?? ""}</Form.Label>
                    <RangeInput id="btn_fs_" min={10} max={25} step={1} defaultValue={fontSize} value={fontSize} onChange={e => onFontSize(e)} />
                </Form.Group>

                <Form.Group className='mb-3'>
                    <Form.Check type="switch" id="qr_status" className="f-between" label={pageContent?.qr_status_label ?? ""} defaultValue={qrStatus} checked={qrStatus} onChange={onQrStatus} reverse />
                </Form.Group>
        </>
    )
}

export default QRAside