import React, { memo, useCallback } from 'react';
import { useParams } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { debounce } from 'lodash';
import { Form } from 'react-bootstrap';
import HtmlParser from 'react-html-parser';
import { SET_FEED_OBJECT } from '../../../../../../../actions/addFeedActions';

const checkboxesByNetwork = {
    18: [
        { className: "hashtagCaption", id: "feedscreatehashtagCaption", key: "hashtagCaption", filter: [26] },
        { className: "hashtagIGTV", id: "feedscreatehashtagIGTV", key: "hashtagIGTV", filter: [26, 23] },
        { className: "onlyReels", id: "feedscreateonlyReels", key: "onlyReels", filter: [26, 23, 24, 1] },
        { className: "hideforBSearch", id: "feedscreatemoderation", key: "moderation", filter: [] }
    ],
    1: [
        // { className: "FeedMultiplePhoto", id: "FeedMultiplePhoto", key: "multiplePhoto", filter: [2, 1, 7, 3, 4, 5] },
        { className: "FeedExcludeRetweet", id: "FeedExcludeRetweet", key: "excludeRetweet", filter: [2, 1, 7, 3, 4, 5] },
        { className: "FeedExcludeReplies", id: "FeedExcludeReplies", key: "excludeReplies", filter: [2, 1, 7, 4, 5] },
        { className: "hideforBSearch", id: "feedscreatemoderation", key: "moderation", filter: [] }
    ],
    0: [
        { className: "hideforBSearch", id: "feedscreatemoderation", key: "moderation", filter: [] }
    ]
};

const CheckBoxList = ({ filterData, pageContent }) => {
    const { networkId } = useParams();
    const dispatch = useDispatch();

    const onInputChange = useCallback(
        debounce((key, value) => {
            dispatch(SET_FEED_OBJECT({ [key]: value }));
        }, 500),
        [dispatch]
    );

    const checkboxList = checkboxesByNetwork[networkId] || checkboxesByNetwork["0"];

    return (
        <>
            {checkboxList.map(data => {
                const desc = pageContent?.[data.key] || "";
                if (data.filter.includes(filterData.filter) || data.filter.length === 0) {
                    return (
                        <div key={data.id} className={`${data.className} mb-lg-3 mb-2`}>
                            <Form.Check
                                type="checkbox"
                                id={`check-inline-${data.key}`}
                                onChange={e => onInputChange(data.key, e.target.checked)}
                                label={HtmlParser(desc)}
                                inline
                            />
                        </div>
                    );
                }
                return null;
            })}
        </>
    );
};

export default memo(CheckBoxList);
