import { useEffect, useMemo } from 'react';
import { Row, Col } from 'react-bootstrap';
import CheckBoxList from './CheckBoxList'
import TabInputTab from './TabInputTab'
import { shallowEqual, useDispatch, useSelector } from 'react-redux';
import TabAccountList from './TabAccountList';
import TabAccountPicker from '../../../AddFeed/component/TabAccountPicker';
import { SET_SELECTED_ACCOUNT } from '../../../../../../../actions/addFeedActions';
import { useNavigate, useParams } from 'react-router-dom';
import AddAccountBlock from '../../component/AddAccountBlock';
import { GET_NETWORK_DATA_BY_ID, showSalesModal } from '../../../../../../../utils';
import TwitterAdvanceTable from '../../../AddFeed/component/twitterAdvanceTable';
import EmptyWithIcon from '../../../../CommonComponents/Empty/EmptyWithIcon';
import { updateSalesModal } from '../../../../../../../actions/loginActions';
import { MediaBaseUrl } from '../../../../../../../actions/api';
import { isAuthRequired } from '../../utils';
import { PRODUCT_PLANS } from '../../../../../../../actions/constants';
import DropZone from '../../component/DropZone';

const RenderBody = ({ filterData, accounts, activeTab, countries, category, content }) => {
    const { networkId } = useParams();
    const filters = [26, 23, 71, 72, 70, 2, 7, 3, 4, 5, 8, 33, 75, 11, 12, 56, 67, 32, 28, 27, 78, 80, 77, 84, 83, 22, 13, 16, 89, 90, 17]

    // ----------check for input field | excluded instagram personal tab with filter id = 1 which is common---------
    if (filters.includes(filterData.filter) || (filterData.filter == 1 && ![3, 18, 28].includes(parseInt(networkId))) || (filterData.filter == 30 && networkId == 18)) {
        return <Row>
            {networkId == 20 ? <Col sm={6}>
                <TabAccountList accounts={accounts} activeTab={activeTab} filterData={filterData} />
            </Col> : null}
            <TabInputTab filterData={filterData} accounts={accounts} activeTab={activeTab} countries={countries} category={category} />{networkId == 12 ? <span className='d-flex justify-content-end'><a href={`${MediaBaseUrl}/media/downloads/sample-xml.txt`} download="sample-xml.txt">Download Sample</a></span> : null}
            {(networkId == 28 && filterData.filter == 72) ? <Col className='mb-1'>
                <TabAccountList accounts={accounts} activeTab={activeTab} filterData={filterData} />
                {accounts?.[0]?.Network?.id != 27 ? <p className='text-center'>{content?.connectAccount?.tiktok_msg}</p> : null}
            </Col> : null}
            </Row>;
    }

    // ----------for account list --------------------
    else return <div className='mb-4'><TabAccountList accounts={accounts} activeTab={activeTab} filterData={filterData} /></div>
}

const TabBody = ({ filterData, accounts, networkDetails, activeTab, countries, category, filterDenyMsg, fileName, setFileName }) => {
    const { networkId } = useParams()
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const userData = useSelector(state => state.authorized?.userData?.user_data, shallowEqual);
    const pageContent = useMemo(() => window?.APP_CONTENT?.add_feed ?? {}, [])
    
    const showAccountPicker = useMemo(() => {
        const filters = [26, 23, 71, 70, 2, 7, 3, 4, 5, 8, 75, 11, 12, 56, 67];
        if (networkId != 28 && (filters.includes(filterData.filter) || (![3, 4, 10, 18, 20].includes(parseInt(networkId))))) return (networkId == 10 && filterData.filter == 23) ? false : true
        return false
    }, [networkId, filterData])

    const denyFilterBtn = useMemo(() =>{
        if(filterDenyMsg && userData?.UserPlan?.planId) {
            const content = window?.APP_CONTENT?.common ?? {};
            const options = { text: content?.upgrade_now ?? "Upgrade Now" , action: () => navigate('/price') };
            if((networkId == 3) || PRODUCT_PLANS.thirdPaid.includes(userData?.UserPlan?.planId ?? "")){
                options["text"] = content?.talk_to_sales ?? "Talk to Sales";
                options["action"] = () => dispatch(updateSalesModal(showSalesModal(userData)));
            }
            return options;
        }
        return null;
    }, [filterDenyMsg, userData, networkId])

    useEffect(() => {
        // ---------------set default account------------
        if (accounts?.length) dispatch(SET_SELECTED_ACCOUNT(accounts[0]))

        return () => {
            if (accounts?.length) dispatch(SET_SELECTED_ACCOUNT(accounts[0]))
        }
    }, [accounts, dispatch]);

    return ( 
        (networkId == 10 && filterData.filter == 18) ? <DropZone {...{ fileName, setFileName }} /> : ((networkDetails.authRequire && !accounts?.length) && isAuthRequired(networkId, filterData?.filter)) ? <AddAccountBlock network={GET_NETWORK_DATA_BY_ID(networkId)} activeTab={activeTab} />
            : filterDenyMsg ?
                denyFilterBtn ? <div className="d-flex flex-column align-items-center h-100 justify-content-center dsb-empty">
                    <EmptyWithIcon title={filterDenyMsg} desc={''} icon={''} button={denyFilterBtn} isFullScreen={false} />
                </div> : null : <>
                    {/* ----------render input/account list for tab-------- */}
                    <RenderBody accounts={accounts} filterData={filterData} activeTab={activeTab} countries={countries} category={category} content={pageContent} />
                    {filterData.filter == 5 && networkId == 1 ? <TwitterAdvanceTable content={pageContent} /> : <></>}

                    <div className='mb-4'>
                        <CheckBoxList filterData={filterData} pageContent={pageContent} />
                    </div>
                    {showAccountPicker ? <div className='mb-4'><TabAccountPicker accounts={accounts} /></div> : <></>}
                </>
    )
}

export default TabBody