import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { shallowEqual, useDispatch, useSelector } from 'react-redux'
import { Card, Button, Row, Col, Alert, Container } from 'react-bootstrap';
import TabInput from './TabInput';
import FeedTitle from './FeedTitle';
import { Link, useNavigate, useParams } from 'react-router-dom'
import { CREATE_FEED_ACTION } from '../../../../../../actions/addFeedActions';
import { FETCH_CONNECTED_ACCOUNTS } from '../../../../../../actions/moderationActions'
import { inputValidationByNetwork, getFeedAlertText, feedByNet, feedByNetFilter } from '../utils'
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import SupportVideo from '../../../../../Layout/Main/CommonComponents/Support/SupportVideo'
import { GET_LOCAL, OPEN_INTERCOM, shopifyApp } from '../../../../../../utils';
import { handleShopifyCallBack } from '../../../../../../actions/planDetail';
import SideMenu from '../../../Moderation/SideMenu';
import AuthHOC from '../../../CommonComponents/AuthHOC';


const FeedBody = ({ network, networkDetails }) => {
    const [isShowVideo, setIsShowVideo] = useState(false);

    const { wallId, networkId } = useParams()
    const navigate = useNavigate()
    const dispatch = useDispatch()
    const { addFeedReducer, feeds, userData } = useSelector(({ addFeedReducer, manageFeeds, authorized }) => ({
        addFeedReducer: addFeedReducer,
        feeds: manageFeeds?.feeds || [],
        userData: authorized?.userData?.user_data
    }), shallowEqual);
    const { createFeedLoading, feedObj } = addFeedReducer;
    const pageContent = useMemo(() => window?.APP_CONTENT?.add_feed ?? {}, [window?.APP_CONTENT])
    const alertMsg = useMemo(() => dispatch(getFeedAlertText(networkId)), [networkId, feedObj.filter])
    const isShopify = useMemo(shopifyApp, [])

    const onCreateFeed = () => {
        const isError = dispatch(inputValidationByNetwork(networkId))
        if (!isError) dispatch(CREATE_FEED_ACTION(networkId, navigate))
    }

    const additionalAction = useCallback((type, code) => {
        if(type == 'ShopifyPlanCallback') handleShopifyCallBack(code, dispatch, navigate);
        else if(type == "bigCommerce") setTimeout(() => navigate(`/widget/accounts/check_session${code}`), 1000);
    }, [dispatch, navigate]);

    const filterDenyMsg = useMemo(() => {
        const {facebookHashtagLimit, facebookhashtag, twitterFeedLimit} = userData?.UserRule || {};
        const filterByFeedCount = feedByNetFilter(feeds, networkId, feedObj.filter);
        if (networkId == 3 && feedObj.filter == 2 && (!facebookhashtag || filterByFeedCount >= facebookHashtagLimit)) return pageContent?.request_facebook ?? "";
        if (networkId == 1) {
            const feedCount = feedByNet(feeds, networkId);
            if (feedCount >= (twitterFeedLimit || 0)) return pageContent?.feed_limit ?? "";
        }
        return "";
    }, [networkId, feedObj.filter, pageContent]);

    const checkTour = async () => {
        const tourStep = GET_LOCAL('tourStep');
        const isTourComplete = GET_LOCAL('isTour') ?? true;
        const { default: appTour } = await import('../../../../../../actions/appTour')
        const { tour } = new appTour(navigate, false, dispatch);
        if (!isTourComplete && tourStep?.id == '#create_feed') setTimeout(() => tour.drive(tourStep?.step ?? 2), 200);
    }

    return (
        <AuthHOC authType="addFeed" authAction={(network) => dispatch(FETCH_CONNECTED_ACCOUNTS(network))} additionalAction={additionalAction}>
            <div className="custom-side-menu custom-feed-content d-flex">
                {!isShopify ? <SideMenu {...{ getWallData: { id: localStorage.getItem('wallId') } }} /> : null}
                <div className="custom-main-content">
                    <div className='d-flex flex-column flex-column-fluid full-content'>
                        {/* <div className="subheader">
                            <Container fluid>
                                <div className='f-between'>
                                    <h1 className="me-2 h4 my-3">Create {GET_NETWORK_NAME(network ? network.id : '')} Feed</h1>
                                </div>
                            </Container>
                        </div> */}
                        <Container className="container-network-type" fluid>
                            <Row className='f-center' style={{ minHeight: 'calc(100vh - 150px)' }}>
                                <Col lg={10} xl={10}>
                                    <Card>
                                        <Card.Body className='p-lg-8'>
                                            <FeedTitle network={network} pageContent={pageContent} />
                                            <TabInput {...{ networkId, networkDetails, filterDenyMsg }} />
                                            {filterDenyMsg ? null : alertMsg ? <Alert variant="warning" className='my-2 border border-warning'>
                                                <div className='d-flex'>
                                                    <FontAwesomeIcon type={'solid'} icon={'circle-info'} iconClass={'fs-6 lh-sm text-warning me-1'} />
                                                    {alertMsg}
                                                </div>
                                            </Alert> : null}
                                        </Card.Body>
                                        {
                                            filterDenyMsg ? null :
                                                <Card.Footer className='px-lg-8'>
                                                    <div className='d-flex justify-content-between flex-wrap'>
                                                        <p className="my-3">
                                                            <span className="d-none d-sm-flex align-items-center text-muted py-1 px-3 rounded-pill badge-light-primary">
                                                                {pageContent?.help_badge ?? ""}
                                                                {/* <Button variant='link' className='f-center ms-2 d-flex' onClick={event => setIsShowVideo(!isShowVideo)}>
                                                                    <FontAwesomeIcon type={'solid'} icon={'play'} iconClass='me-1' />
                                                                    Watch Video
                                                                </Button>
                                                                <span className='mx-1'>Or</span> */}
                                                                <Button variant='link' className='f-center ms-2 d-flex' onClick={OPEN_INTERCOM}>
                                                                    <FontAwesomeIcon type={'solid'} icon={'comments'} iconClass='me-1' />
                                                                    {pageContent?.connect_support ?? ""}
                                                                </Button>
                                                            </span>
                                                        </p>

                                                        <div className='d-flex justify-content-end align-items-center p-2'>
                                                            <Link to={`/content/addfeed/${wallId}`} className="btn btn-secondary" title='back'>{pageContent?.btn_back ?? ""}</Link>
                                                            <Button id="create_feed" className={`ms-3 ${createFeedLoading ? 'spinner' : ''} `} onClick={onCreateFeed}>{pageContent?.btn_create_feed ?? ""}</Button>
                                                        </div>
                                                    </div>
                                                </Card.Footer>
                                        }
                                    </Card>
                                </Col>
                            </Row>
                        </Container>
                        <SupportVideo show={isShowVideo} link={'https://player.vimeo.com/video/907703540?autoplay=1&loop=1&autopause=0'} onClose={event => setIsShowVideo(!isShowVideo)} />
                    </div>
                </div>
            </div>
        </AuthHOC>
    )

}

export default FeedBody